import { Fragment, useCallback, useEffect } from "react";
import BorderBottom from "../../Ui/Border/BorderBottom";
import style from "../../UserStudentMember/StudentProfile/StudentProfile.module.css";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import { useTranslation } from "react-i18next";
import Loading from "../../Loader/Loading";
import { useStaffProfileAboutDetail } from "../../Hooks/ApiHooks/InstituteStaff/institute-staff-api";

const array_field = [
  "staff_qualification_details",
  "staff_research_and_publication",
  "staff_past_experience_details",
];

const StaffDynamicProfileAbout = ({ staffId }) => {
  const { t } = useTranslation();
  const { staffProfileAboutDetail, staffProfileAboutDetailLoading } =
    useStaffProfileAboutDetail({
      data: {
        sid: staffId,
      },
      skip: !staffId,
    });

  const onViewFile = useCallback((iKey) => {
    if (iKey) {
      const newWindow = window.open(
        `${imageShowUrl}/${iKey}`,
        "_blank",
        "noopener,noreferrer"
      );
      if (newWindow) newWindow.opener = null;
    }
  }, []);
  return (
    <>
      <section className={style.sdpa}>
        {staffProfileAboutDetail?.map((sl, index) => (
          <div className={style.sdpa_section} key={`${sl?.key}${index}`}>
            <h5>{sl?.key ?? ""}</h5>
            {array_field?.includes(sl?.fields?.[0]?.form_checklist_key) ? (
              <>
                {sl?.fields?.[0]?.form_checklist_key ===
                  "staff_qualification_details" &&
                  sl?.fields?.[0]?.value?.map((st, index) => (
                    <div
                      className={style.sdpac_wrap}
                      key={`${sl?.fields?.[0]?.form_checklist_key}${index}`}
                    >
                      <div className={style.sdpas_container}>
                        <p className={style.sdpasc_label}>Institute Name :</p>
                        <h6 className={style.sdpasc_value}>
                          {" "}
                          {st?.institute_name ?? ""}
                        </h6>
                      </div>
                      <div className={style.sdpas_container}>
                        <p className={style.sdpasc_label}>Degree Name :</p>
                        <h6 className={style.sdpasc_value}>
                          {" "}
                          {st?.degree_name ?? ""}
                        </h6>
                      </div>
                      <div className={style.sdpas_container}>
                        <p className={style.sdpasc_label}>Passing Year :</p>
                        <h6 className={style.sdpasc_value}>
                          {" "}
                          {st?.passing_year ?? ""}
                        </h6>
                      </div>
                    </div>
                  ))}
                {sl?.fields?.[0]?.form_checklist_key ===
                  "staff_past_experience_details" &&
                  sl?.fields?.[0]?.value?.map((st, index) => (
                    <div
                      className={style.sdpac_wrap}
                      key={`${sl?.fields?.[0]?.form_checklist_key}${index}`}
                    >
                      <div className={style.sdpas_container}>
                        <p className={style.sdpasc_label}>Institute Name :</p>
                        <h6 className={style.sdpasc_value}>
                          {" "}
                          {st?.institute_name ?? ""}
                        </h6>
                      </div>
                      <div className={style.sdpas_container}>
                        <p className={style.sdpasc_label}> Designation :</p>
                        <h6 className={style.sdpasc_value}>
                          {st?.designation ?? ""}
                        </h6>
                      </div>
                      <div className={style.sdpas_container}>
                        <p className={style.sdpasc_label}>From :</p>
                        <h6 className={style.sdpasc_value}>{st?.from ?? ""}</h6>
                      </div>
                      <div className={style.sdpas_container}>
                        <p className={style.sdpasc_label}>To :</p>
                        <h6 className={style.sdpasc_value}>{st?.to ?? ""}</h6>
                      </div>
                    </div>
                  ))}

                {sl?.fields?.[0]?.form_checklist_key ===
                  "staff_research_and_publication" &&
                  sl?.fields?.[0]?.value?.map((st, index) => (
                    <div
                      className={style.sdpac_wrap}
                      key={`${sl?.fields?.[0]?.form_checklist_key}${index}`}
                    >
                      <div className={style.sdpas_container}>
                        <p className={style.sdpasc_label}>Title :</p>
                        <h6 className={style.sdpasc_value}>
                          {st?.title ?? ""}
                        </h6>
                      </div>
                      <div className={style.sdpas_container}>
                        <p className={style.sdpasc_label}> Link :</p>
                        <h6 className={style.sdpasc_value}>
                          {st?.link_url ?? ""}
                        </h6>
                      </div>
                      <div className={style.sdpas_container}>
                        <p className={style.sdpasc_label}>Description :</p>
                        <h6
                          className={style.sdpasc_value}
                          style={{
                            whiteSpace: "break-spaces",
                          }}
                        >
                          {" "}
                          {st?.description ?? ""}
                        </h6>
                      </div>
                      <div className={style.sdpas_container}>
                        <p className={style.sdpasc_label}>Attachment :</p>
                        {st?.attachment ? (
                          <h6
                            className={style.sdpasc_value}
                            onClick={() => onViewFile(st?.attachment)}
                            style={{
                              cursor: "pointer",
                              color: "#002BBE",
                            }}
                          >
                            {t("view_file")}
                          </h6>
                        ) : null}
                      </div>
                    </div>
                  ))}
              </>
            ) : (
              <div className={style.sdpac_wrap}>
                {sl?.fields?.map((fl, f_index) =>
                  fl?.form_checklist_typo === "FILE" ? (
                    <div
                      className={style.sdpas_container}
                      key={`${sl?.form_checklist_key}${f_index}`}
                    >
                      <p className={style.sdpasc_label}>
                        {fl?.form_checklist_lable ?? ""} :
                      </p>
                      {fl?.value ? (
                        <h6
                          className={style.sdpasc_value}
                          onClick={() => onViewFile(fl?.value)}
                          style={{
                            cursor: "pointer",
                            color: "#002BBE",
                          }}
                        >
                          {t("view_file")}
                        </h6>
                      ) : null}
                    </div>
                  ) : fl?.form_checklist_key ===
                    "studentProfilePhoto" ? null : (
                    <div
                      className={style.sdpas_container}
                      key={`${sl?.form_checklist_key}${f_index}`}
                    >
                      <p className={style.sdpasc_label}>
                        {fl?.form_checklist_lable ?? ""} :
                      </p>
                      <h6 className={style.sdpasc_value}>{fl?.value ?? ""}</h6>
                    </div>
                  )
                )}
              </div>
            )}

            <BorderBottom />
          </div>
        ))}
      </section>
      {staffProfileAboutDetailLoading && <Loading />}
    </>
  );
};

export default StaffDynamicProfileAbout;
